@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    overflow: hidden;
}
.home img {
    width: 1200px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.home h1 {
    font-family: "Poppins", sans-serif;
    font-family: "Roboto", sans-serif;
    font-family: "Open Sans", sans-serif;
    color: #02589c;
}
.docs-link {
    margin-top: 10px;
    font-size: 20px;
    text-decoration: none;
    color: #649ad2;
    font-family: "Open sans";
    transition: 0.3s all ease-in-out;
}
.docs-link:hover {
    color: #014482;
}

@media (max-width: 1230px) {
    .home img {
        width: 900px;
    }
}
@media (max-width: 960px) {
    .home img {
        width: 600px;
    }
    .home h1 {
        font-size: 23px;
    }
    .docs-link {
        font-size: 15px;
    }
}
@media (max-width: 650px) {
    .home img {
        width: 400px;
    }
    .home h1 {
        font-size: 15px;
    }
    .docs-link {
        font-size: 13px;
    }
}

.docs {
    display: flex;
    justify-content: center;
    padding: 0px 380px;
    /* overflow-x: hidden; */
}
.docs-content {
    display: flex;
    /* width: 65%; */
    margin-top: 55px;
    /* background-color: #f5f5f5; */
    padding: 50px;
    padding-top: 0px;
}
.docs-info {
    padding: 10px 40px;
    max-width: 80%;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}
.docs-info h1,
.docs-info h2,
.docs-info h3,
.docs-info h4,
.docs-info h5,
.docs-info h6 {
    font-family: "Open Sans";
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
    margin-bottom: 5px;
    font-weight: 500;
    border-bottom: 1px solid #eaecef;
    padding-bottom: 5px;
    margin-bottom: 15px;
    margin-top: 20px;
}
.docs-info a {
    text-decoration: none;
    color: #0366d6;
}
.docs-info ol {
    margin-left: 25px;
    margin-top: 10px;
}
.docs-info li {
    margin-bottom: 5px;
}

@media (max-width: 1050px) {
    /* TODO add burguer menu */
    .sidebar {
        display: none;
    }
}

.sidebar {
    border-right: 1px solid #dbdbdb;
    width: 250px;
}
.sidebar ul {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
    list-style: none;
}
.sidebar ul li p {
    margin-left: 5px;
    /* padding-left: 5px; */
    padding: 5px;
    cursor: pointer;
    /* margin-bottom: 2px; */
    padding-top: 2px;
    transition: 0.3s background-color ease-in-out;
}
.sidebar ul li p:hover {
    background-color: #eeeeee;
}
.sidebar ul li h4 {
    margin-top: 10px;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
    font-weight: 500;
    font-size: 17.5px;
}
.selected-topic {
    background-color: #e4e4e4;
}
.selected-topic:hover {
    background-color: #e4e4e4 !important;
}

.header {
    /* background-color: #02589c; */
    background-color: #649ad2;
    /* background-color: #014482; */
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    top: 0;
    width: 100%;
}
.header-content {
    display: flex;
    align-items: center;
    width: 80%;
    font-family: "Ubuntu mono", "Open Sans";
    color: #fff;
    /* background-color: lightcoral; */
}
.header-title {
    /* background-color: lightgray; */
    flex: 3 1;
}
.header-links {
    display: flex;
    justify-content: space-around;
    flex: 1 1;
    /* background-color: lightpink; */
}
.header-links a {
    text-decoration: none;
    color: #fff;
    font-size: 22px;
}
